import { writable } from 'svelte/store';
import db from './firebase/firebase';
const createEmployeeList = () => {
    const { subscribe, set } = writable([]);
    const updateStore = () => {
        db.get().then(querySnapshot => {
            const employeeList = [];
            querySnapshot.forEach(element => employeeList.push(Object.assign(Object.assign({}, element.data()), { id: element.id })));
            set(employeeList.sort((a, b) => a.name.localeCompare(b.name, 'ua')));
        });
    };
    const updateEmployeeInfo = (employeeId, newEmployeeData) => db.doc(employeeId).update(newEmployeeData).then(updateStore);
    updateStore();
    return {
        subscribe,
        fireEmployee: (employeeId) => db.doc(employeeId).delete().then(updateStore),
        changeEmployeeInfo: (employeeId, newEmployeeInfo) => updateEmployeeInfo(employeeId, newEmployeeInfo),
        changeEmployeeVacationDays: (selectedEmployee, selectedVacation, daysToDeduct) => updateEmployeeInfo(selectedEmployee.id, {
            vacations: selectedEmployee.vacations.map(vacation => {
                if (vacation.type !== selectedVacation.type)
                    return vacation;
                return Object.assign(Object.assign({}, vacation), { vacationDays: vacation.vacationDays - daysToDeduct });
            }),
        }),
        changeEmployeeTotalVacationDays: (selectedEmployee, selectedVacation, newTotalVacationDays) => updateEmployeeInfo(selectedEmployee.id, Object.assign(Object.assign({}, selectedEmployee), { vacations: selectedEmployee.vacations.map(vacation => {
                if (vacation.type !== selectedVacation.type)
                    return vacation;
                return Object.assign(Object.assign({}, vacation), { totalDays: newTotalVacationDays, vacationDays: newTotalVacationDays });
            }) })),
        addEmployee: (employeeToAdd) => db.add(employeeToAdd).then(updateStore),
        addVacationsGroup: (selectedEmployee, newVacationsGroup) => updateEmployeeInfo(selectedEmployee.id, { vacations: [...selectedEmployee.vacations, newVacationsGroup] }),
        changeEmployeeType: (employeeToChange, newEmployeeType) => updateEmployeeInfo(employeeToChange.id, { employeeType: newEmployeeType }),
    };
};
export const employeeList = createEmployeeList();
export const vacationTypes = {
    main: 'Основна',
    forSpecialNatureOfWork: 'За особливий характер праці',
    social: 'Соціальна',
    forEmployeeWish: 'За бажанням працівника',
    byAgreement: 'За згодою сторін',
};
export const employeeTypes = {
    administration: 'Адміністрація',
    fired: 'Звільнені',
    serviceStaff: 'Обслуговуючий персонал',
    supportStaff: 'Допоміжний персонал',
    teacher: 'Вчителі',
    technicalStaff: 'Технічний персонал',
};
const createEmployeeTypeFilter = () => {
    const { subscribe, set } = writable(null);
    return { subscribe, setType: (newType) => set(newType) };
};
const createExpandedEmployeeCardId = () => {
    const { subscribe, set } = writable('');
    return { subscribe, setId: (contentId) => set(contentId) };
};
export const expandedEmployeeCardId = createExpandedEmployeeCardId();
export const employeeTypeFilter = createEmployeeTypeFilter();
